.dialogs {
    display: grid;

    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    border-right: solid grey;
}

.dialogsItems a:hover {
    color: #3e8fa0;
}

.dialogsItems a.active {
    color: gold;
}

.dialog a {
    color: #fff;
    text-decoration: none;
}

.dialog {
    margin: 30px 20px;
    color: #fff;
}

.dialogsItems .active a {
    color: gold;
}

.messages {
    margin: 20px;
    color: #fff;
}

